export const getStudent = async (id) => {
  try {
    let querySnapshot = await app.firestore().collection('students').doc(id).get();
    const student = querySnapshot.data()
    student.id = id
    let products = []
    if(typeof(student.products)==="undefined"){
      student.products = products
      return student;
    }
    for await (let valp of student.products){
      const product = await valp.get();
      products.push(product.id)
    }
    student.products = products

    return student
  } catch (err) {
    console.log(err);
  }
}

export const updateStudent = async (data) => {
  if (data.birthdate._isAMomentObject) {
    data.birthdate = getFirestore().Timestamp.fromDate(data.birthdate.toDate())
  }
  try {
    let id = data.id;
    let result = await app.firestore().collection('students').doc(id).update(data);
    
    return result
  } catch (err) {
    console.log(err);
  }
};


