import React from 'react';
import PropTypes from 'prop-types';
import clsx from 'clsx';
import * as Yup from 'yup';
import { Formik } from 'formik';
import { useSnackbar } from 'notistack';
import {
  Box,
  Button,
  Card,
  CardContent,
  Grid,
  TextField,
  makeStyles,
  Tooltip
} from '@material-ui/core';
import { KeyboardDatePicker } from '@material-ui/pickers';
import wait from 'src/utils/wait';
import {updateStudent} from '../../../api/students.js';
import Label from 'src/components/Label.js';
import moment from 'moment';
const useStyles = makeStyles(() => ({
  root: {}
}));

const genders = [
  {
    id: 'male',
    name: 'Nam'
  },
  {
    id: 'female',
    name: 'Nữ'
  }
];
const StudentEditForm = ({
  className,
  student,
  ...rest
}) => {
  const birthdate = null
  if(student.birthdate){
    birthdate = student.birthdate.seconds*1000;
  }
  const classes = useStyles();
  const { enqueueSnackbar } = useSnackbar();
    return (
    <Formik
    initialValues={{
      id: student.id || '',
      name: student.name || '',
      class_code: student.class_code || '',
      birthdate: moment(birthdate) || new Date(),
      parent_name: student.parent_name || '',
      parent_phone: student.parent_phone || '',
      submit: null
    }}
      validationSchema={Yup.object().shape({
        name: Yup.string().max(255).required('Name is required'),
        code: Yup.string().max(15),
        years: Yup.string().max(15),
      })}
      onSubmit={async (values, {
        setErrors,
        setStatus,
        setSubmitting
      }) => {
        try {
          values.school_id = localStorage.getItem('schoolid');
          console.log(values)
          updateStudent(values)
          // NOTE: Make API request
          await wait(500);
          setStatus({ success: true });
          setSubmitting(false);
          enqueueSnackbar('Class updated', {
            variant: 'success',
            action: <Button>See all</Button>
          });
        } catch (err) {
          console.error(err);
          setStatus({ success: false });
          setErrors({ submit: err.message });
          setSubmitting(false);
        }
      }}
    >
      {({
        errors,
        handleBlur,
        handleChange,
        handleSubmit,
        isSubmitting,
        setFieldValue,
        setFieldTouched,
        touched,
        values
      }) => (
        <form
          className={clsx(classes.root, className)}
          onSubmit={handleSubmit}
          {...rest}
        >
          <Card>
            <CardContent>
              <Grid
                container
                spacing={3}
              >
                <Grid
                  item
                  md={6}
                  xs={12}
                >
                  <TextField
                    error={Boolean(touched.name && errors.name)}
                    fullWidth
                    helperText={touched.name && errors.name}
                    label="Tên học sinh"
                    name="name"
                    onBlur={handleBlur}
                    onChange={handleChange}
                    required
                    value={values.name}
                    variant="outlined"
                  />
                </Grid>
                <Grid
                  item
                  md={6}
                  xs={12}
                >
                  
                  <TextField
                    error={Boolean(touched.class_code && errors.class_code)}
                    fullWidth
                    helperText={touched.class_code && errors.class_code}
                    label="Lớp"
                    name="class_code"
                    onBlur={handleBlur}
                    onChange={handleChange}
                    required
                    value={values.class_code}
                    disabled
                    variant="outlined"
                  />
                </Grid>
                <Grid
                  item
                  md={6}
                  xs={12}
                >
                  <TextField
                    fullWidth
                    label="Giới tính"
                    name="gender"
                    onChange={handleChange}
                    select
                    SelectProps={{ native: true }}
                    value={values.gender}
                    variant="outlined"
                  >
                    {genders.map((gender) => (
                      <option
                        key={gender.id}
                        value={gender.id}
                      >
                        {gender.name}
                      </option>
                    ))}
                  </TextField>
                </Grid>
                <Grid
                  item
                  md={6}
                  xs={12}
                >
                 <KeyboardDatePicker
                className={classes.datePicker}
                label="Ngày sinh (ngày/tháng/năm)"
                format="DD/MM/YYYY"
                name="birthdate"
                inputVariant="outlined"
                value={values.birthdate}
                onBlur={() => setFieldTouched('birthdate')}
                onClose={() => setFieldTouched('birthdate')}
                onAccept={() => setFieldTouched('birthdate')}
                onChange={(date) => setFieldValue('birthdate', date)}
              />
                </Grid>
                <Grid
                  item
                  md={6}
                  xs={12}
                >
                  <TextField
                    error={Boolean(touched.parent_name && errors.parent_name)}
                    fullWidth
                    helperText={touched.parent_name && errors.parent_name}
                    label="Tên phụ huynh"
                    name="parent_name"
                    onBlur={handleBlur}
                    onChange={handleChange}
                    value={values.parent_name}
                    variant="outlined"
                  />
                </Grid>
                <Grid
                  item
                  md={6}
                  xs={12}
                >
                  <TextField
                    error={Boolean(touched.parent_phone && errors.parent_phone)}
                    fullWidth
                    helperText={touched.parent_phone && errors.parent_phone}
                    label="Số điện thoại phụ huynh"
                    name="parent_phone"
                    onBlur={handleBlur}
                    onChange={handleChange}
                    value={values.parent_phone}
                    variant="outlined"
                  />
                </Grid>
                <Grid item />
                
              </Grid>
              <Box mt={2}>
                <Button
                  variant="contained"
                  color="secondary"
                  type="submit"
                  disabled={isSubmitting}
                >
                  Lưu
                </Button>
              </Box>
            </CardContent>
          </Card>
        </form>
      )}
    </Formik>
  );
};

StudentEditForm.propTypes = {
  className: PropTypes.string,
  student:PropTypes.object.isRequired
};

export default StudentEditForm;
